import './Stack.scss';

const Stack = ()=> {
  return(
    <div id="stackOutbox">
      <div className='stackInbox'>
        <div id="devTool">
          <div className="imgGroup">
            <div className="html">
              <img src={ require('../img/1HTML.png') } alt="html logo"/>
              <p>HTML</p>
            </div>
            <div className="css">
              <img src={ require('../img/2CSS.png') } alt="css logo"/>
              <p>CSS</p>
            </div>
            <div className="js">
              <img src={ require('../img/3JS.png') } alt="js logo"/>
              <p>JAVASCRIPT</p>
            </div>
            <div className="react">
              <img src={ require('../img/4react.png') } alt="react logo"/>
              <p>REACT</p>
            </div>
          </div>
          <div className="devText">
            <p><span className="dot-4px"></span>HTML :       .</p>
            <p><span className="dot-4px"></span>CSS :         .</p>
            <p><span className="dot-4px"></span>JavaScript :        .</p>
            <p><span className="dot-4px"></span>React : SPA          .</p>
          </div>
        </div> {/* devTool end */}

        <div id="desiTool">
          <div className="imgGroup">
            <div className="figma">
              <img src={ require('../img/5figma.png') } alt="figma logo"/>
              <p>FIGMA</p>
            </div>
            <div className="il">
              <img src={ require('../img/6il.png') } alt="ilustratior logo"/>
              <p>ILUSTRATOR</p>
            </div>
            <div className="photo">
              <img src={ require('../img/7photo.png') } alt="photoshop logo"/>
              <p>PHOTOSHOP</p>
            </div>
            <div className="pre">
              <img src={ require('../img/8pre.png') } alt="premiere pro logo"/>
              <p>PREMIERE PRO</p>
            </div>
          </div>
          <div className="desiText">
            <p><span className="dot-4px"></span>Figma :     , UI   </p>
            <p><span className="dot-4px"></span> Ilustrator :       .</p>
            <p><span className="dot-4px"></span>Photoshop :    ,     .</p>
            <p><span className="dot-4px"></span>Premeiere Pro :      .</p>
          </div>
        </div> {/* desiTool end */}
      </div>  {/* inbox end */}
    </div>
  );
}
export default Stack;